@import "tools/fonts";
@import "tools/libs";
@import "tools/vars";
@import "tools/mixins";
@import "tools/extends";
@import "tools/keyframes";
@import "tools/smart-grid";
@import "tools/media";
@import "tools/buttons";
@import "tools/inputs";

@include reset();

.container {
    @include wrapper(); }

.row {
    @include row-flex(); }

.icon {
    fill: #000000; }

.section-title {
    font-size: 36px;
    font-weight: 700;
    text-align: center; }
body {
    color: $text-primary;
    font-family: 'Montserrat', sans-serif;
    font-size: 14px; }
a {
    text-decoration: none; }

.form {
    .input-wrapper {
        @include to(450px) {
            width: 100%; } }
    &__input {
        @include to(450px) {
            width: 100%;
 } }            // min-width: 290px
    .btn {
        @include to(450px) {
            width: 100% !important; } } }

.section-title {
    @include sm(font-size, 28px);
    @include xs(font-size, 22px); }

.header {
    background-color: $palette-dark;
    .row {
        height: 100px;
        align-items: center;
        @include to(368px) {
            height: 120px; } }
    .logo-wrapper {
        @include col();
        @include size(4);
        @include to(640px) {
            @include size(6); }
        @include to(368px) {
            @include size(12); }
        .logo {
            @include to(368px) {
                margin: auto;
                display: block; } } }
    .work-time {
        @include col();
        @include size(2);
        @include shift-left(1);
        text-align: center;
        font-size: 12px;
        white-space: pre-line;
        @include to(640px) {
            display: none; }
        @include md-block() {
            @include size(4);
            margin: auto; } }
    .call-us {
        @include col();
        @include size(5);
        @include size-md(4);
        @include to(640px) {
            @include size(6); }
        @include to(368px) {
            @include size(12); }
        display: flex;
        .phones {
            margin-left: auto;
            margin-right: 30px;
            display: flex;
            flex-direction: column;
            justify-content: center;
            @include to(1011px) {
                display: none; }
            &__phone {
                font-size: 18px;
                font-weight: 700;
                margin-bottom: 14px;
                &:last-child {
                    margin-bottom: 0; } } }
        .btn {
            @include to(1011px) {
                margin-left: auto; }
            @include to(640px) {
                margin: auto; }
            @include to(440px) {
                padding: 15px; } } } }

// Section home

.home {
    background: eurl('header_bg.jpg') no-repeat center center / cover;
    height: 860px;
    @include to(440px) {
        height: auto; }
    .hero {
        @include col();
        @include size(6);
        @include size-lg(7);
        @include size-md(12);
        @include to(440px) {
            padding-bottom: 50px; }
        @include sm(padding, 20px);
        color: $text-secondary;
        padding: 100px 124px 125px 100px;
        @include lg(padding, 50px 50px 125px 50px);
        background-color: $palette-secondary;
        &__title, &__subtitle {
            font-weight: 800; }
        &__title {
            font-size: 40px;
            @include xl(font-size, 35px);
            @include lg(font-size, 30px);
            @include to(440px) {
                font-size: 22px;
                margin-bottom: 20px; }
            margin-bottom: 49px;
            text-transform: uppercase;
            word-wrap: normal; }
        &__subtitle {
            font-size: 25px;
            @include lg(font-size, 20px);
            @include md(font-size, 16px); }
        .advantages-list {
            margin-top: 100px;
            @include to(440px) {
                margin-top: 30px; }
            .advantge {
                display: flex;
                align-items: center;
                margin-bottom: 48px;
                &:last-child {
                    margin-bottom: 0; }
                .icon {
                    min-width: 30px;
                    max-width: 30px;
                    min-height: 30px;
                    max-height: 30px; }
                &__text {
                    margin-left: 26px;
                    @include md(font-size, 16); } } } } }

// Section request price

.request-price {
    background: eurl('wide_belaz_bg.jpg') no-repeat center center / contain;
    color: #000000;
    max-height: 500px;
    padding: 100px 0;
    .content-wrapper {
        @include col();
        @include size(10);
        @include shift-left(2);
        @include to(1095px) {
            margin-left: auto; }
        @include to(673px) {
            margin: auto;
            @include size(12); }
        justify-content: space-around;
        display: flex;
        flex-direction: column;
        .section-title {
            white-space: pre-line;
            font-weight: 900;
            font-size: 30px;
            text-align: right;
            margin-left: auto;
            @include to(460px) {
                font-size: 24px; }
            @include to(673px) {
                text-align: center;
                margin: auto;
                white-space: normal; } } }

    .form {
        display: flex;
        flex-direction: column;
        margin-top: 60px;
        .input-wrapper {
            margin-left: 30px;
            @include to(450px) {
                width: 100%; } }
        &__input {
            @include to(450px) {
                width: 100%; } }
        &-top {
            display: flex;
            flex-direction: row;
            justify-content: flex-end;
            @include to(1095px) {
                flex-direction: column;
                align-items: flex-end;
                .input-wrapper {
                    margin-left: 0;
                    margin-bottom: 20px; } }
            @include to(673px) {
                align-items: center; } }
        &-bottom {
            display: flex;
            flex-direction: row;
            margin-top: 30px;
            @include to(1095px) {
                flex-direction: column-reverse;
                margin-top: 0; }
            .note {
                display: flex;
                margin-right: 30px;
                align-items: center;
                margin-left: auto;
                @include to(1095px) {
                    margin-right: 0;
                    margin-top: 10px; }
                @include to(673px) {
                    margin: 20px auto; }
                &__text {
                    font-weight: 700;
                    width: 230px;
                    text-align: right;
                    margin-right: 30px;
                    @include to(673px) {
                        text-align: center;
                        margin-right: 10px; } }
                .icon {
                    max-width: 30px;
                    max-height: 30px; } }
            .btn {
                align-self: flex-end;
                width: 370px;
                height: 70px;
                @include to(673px) {
                    align-self: center; }
                @include to(450px) {
                    width: 100%; } } } } }


// Section advantages

.advantages {
    background: $palette-primary;
    padding: 100px 0;
    @include md(padding, 30px 0);
    box-sizing: border-box;
    .section-title {
        font-size: 36px;
        @include sm(font-size, 28px);
        font-weight: 700;
        text-align: center;
        width: 100%; }
    .subtitle {
        font-size: 20px;
        text-align: center;
        margin: 0 auto;
        margin-top: 30px;
        white-space: pre-line; }
    .advantages-list {
        display: flex;
        margin-top: 105px;
        @include sm(margin-top, 40px);
        flex-wrap: wrap;
        width: 100%;
        .advantage {
            @include col();
            @include size(3);
            @include to(1020px) {
                @include size(6); }
            @include sm-block() {
                @include size(12);
                flex-direction: row;
                margin-bottom: 20px; }
            display: flex;
            flex-direction: column;
            align-items: center;
            &__text {
                font-size: 18px;
                text-align: center;
                width: 250px;
                margin-top: 50px;
                @include to(1020px) {
                    margin-top: 10px;
                    margin-bottom: 35px; }
                @include sm-block() {
                    margin: 0;
                    text-align: left;
                    margin-left: 20px;
                    font-size: 16px; } }
            .icon {
                fill: #ffffff;
                max-width: 100px;
                max-height: 100px;
                @include sm-block() {
                    max-width: 60px;
                    max-height: 60px; } } } } }

.find-detail {
    background: eurl('blue_belaz_lb.png') no-repeat left bottom, eurl('blue_belaz_rt.png') no-repeat right top, eurl('blue_belaz_pattern.png');
    @include md-block() {
        background: eurl('blue_belaz_lb.png') no-repeat left bottom/50%, eurl('blue_belaz_rt.png') no-repeat right top/50%, eurl('blue_belaz_pattern.png'); }
    padding: 100px 0;
    @include md(padding, 30px 0);
    white-space: pre-line;
    .section-title {
        font-size: 30px;
        @include sm(font-size, 28px);
        font-weight: 700;
        width: 100%;
        text-align: center; }
    .subtitle {
        margin-top: 30px;
        font-size: 20px;
        text-align: center;
        width: 60%;
        margin: 0 auto;
        @include md(width, 100%); }
    .form {
        @include col();
        @include size(12);
        display: flex;
        margin-top: 60px;
        @include md(margin-top, 30px);
        justify-content: space-between;
        @include xl-block() {
            flex-direction: column;
            align-items: center; }
        .input-wrapper {
            @include xl-block() {
                margin-bottom: 10px; } }
        .btn {
            width: 370px;
            height: 70px; } }
    .recall-text {
        margin-top: 60px;
        text-align: center;
        width: 100%;
        b {
            font-size: 30px;
            margin: 16px; }
        br {
            margin-bottom: 30px;
            &:last-child {
                margin-bottom: 0; } } } }


// Section Safe shipping
.safe-shipping {
    color: $text-secondary;
    padding: 100px 0;
    @include md(padding, 30px 0);
    .section-title {
        font-weight: 700;
        width: 100%;
        text-align: center;
        font-size: 36px;
        @include sm(font-size, 28px); }
    .safe-shipping-list {
        display: flex;
        margin-top: 100px;
        @include sm(margin-top, 30px);
        @include to(813px) {
            flex-direction: column;
            width: 100%; }
        .list-item {
            @include col();
            @include size(4);
            @include to(813px) {
                @include size(12);
                align-items: center;
                margin-bottom: 40px;
                &:last-child {
                    margin-bottom: 0; } }
            display: flex;
            flex-direction: column;
            justify-content: center;
            text-align: center;
            align-items: center;
            &__img {
                min-height: 300px;
                min-width: 230px; }
            &__description {
                font-size: 18px;
                margin-top: 40px;
                @include to(813px) {
                    margin-top: 10px; } }
            &__more {
                font-weight: 700;
                font-size: 18px;
                text-decoration: underline;
                color: $text-secondary;
                margin-top: 40px;
                @include to(813px) {
                    margin-top: 10px; } } } } }
.discounts {
    padding: 100px 0;
    @include md(padding, 30px 0);
    background-color: $palette-primary;
    .section-title {
        @include col();
        @include size(10);
        @include shift(1);
        @include sm(font-size, 28px);
        @include md-block() {
            margin: auto;
            width: 100%; } }
    &-list {
        display: flex;
        margin-top: 100px;
        @include sm(margin-top, 40px);
        flex-wrap: wrap;
        .list-item {
            @include col();
            @include size(3);
            @include to(802px) {
                @include size(6);
                margin-bottom: 30px; }
            @include to(450px) {
                @include size(12); }
            display: flex;
            flex-direction: column;
            align-items: center;
            text-align: center;
            &__circle {
                width: 100px;
                height: 100px;
                border: 2px solid $text-primary;
                border-radius: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                margin-bottom: 40px;
                @include to(802px) {
                    margin-bottom: 10px; }
                .icon {
                    fill: $text-primary;
                    max-width: 56px;
                    max-height: 39px; } }
            &__circle-text {
                font-weight: 700;
                font-size: 30px; } } } }

.leave-questions {
    background-color: $palette-primary-light;
    padding: 100px 0;
    @include md(padding, 30px 0);
    display: none;
    .section-title, .subtitle {
        text-align: center;
        width: 100%; }
    .subtitle {
        margin: 30px 0 100px 0;
        font-size: 20px; }
    .questions-list {
        @include col();
        @include size(5);
        display: flex;
        flex-direction: column;
        &__item {
            margin-bottom: 55px;
            font-weight: 700;
            text-decoration: underline;
            padding-left: 40px;
            cursor: pointer;
            &:last-child {
                margin-bottom: 0; } } }
    .director {
        @include col();
        @include size(6);
        @include shift-left(1);
        height: 390px;
        background: eurl('director.jpg') no-repeat center center / contain;
        position: relative;
        &__job-title-bg {
            background-color: transparentize($palette-primary-light, .2);
            width: 230px;
            padding: 12px 29px 11px 19px;
            position: absolute;
            bottom: 0;
            left: 0; } } }

// Section Comfort-Work

.comfort-work {
    color: $text-secondary;
    padding: 100px 0;
    @include md(padding, 30px 0);
    .section-title, .subtitle {
        width: 100%;
        text-align: center; }
    .subtitle {
        font-size: 20px;
        padding-top: 30px; }
    .road {
        display: flex;
        text-align: center;
        width: 100%;
        padding-top: 100px;
        @include sm(padding-top, 40px);
        @include to(842px) {
            flex-wrap: wrap; }
        &-item {
            @include col();
            @include size(4);
            @include to(842px) {
                @include size(12); }
            display: flex;
            flex-direction: column;
            align-items: center;
            &__img {
                width: 88px;
                height: 88px; }
            &__description {
                padding-top: 49px;
                font-size: 18px;
                white-space: pre-line;
                @include to(842px) {
                    white-space: normal;
                    padding-top: 15px;
                    padding-bottom: 30px; } } } } }

// Section our stock

.our-stcok {
    background-color: $palette-primary-light;
    padding: 100px 0;
    @include md(padding, 30px 0);
    .section-title, .subtitle {
        width: 100%;
        text-align: center; }
    .subtitle {
        font-size: 20px;
        padding-top: 30px; }
    .stock-photos {
        margin-top: 60px;
        display: flex;
        width: 100%;
        @include md(flex-direction, column);
        .stock-photo {
            @include col();
            @include size(4);
            @include size-md(12);
            @include md-block() {
                margin-bottom: 20px;
                &:last-child {
                    margin-bottom: 0; } }
            &__img {
                @include md(display, block);
                @include md(margin, auto); } } } }

// Section Mobile Team
.mobile-team {
    padding: 100px 0;
    @include md(padding, 30px 0);
    color: $text-secondary;
    white-space: pre-line;
    .section-title {
        width: 100%;
        @include sm(font-size, 28px); }
    .road {
        padding-top: 100px;
        @include md(padding-top, 30px);
        width: 100%;
        display: flex;
        @include to(914px) {
            flex-direction: column; }
        .road-item {
            @include col();
            @include size(4);
            @include to(914px) {
                flex-direction: row;
                @include size(8);
                @include shift(2); }
            @include to(610px) {
                @include size(10);
                @include shift(1); }
            @include to(500px) {
                @include size(12);
                margin: auto;
                margin-bottom: 30px; }
            @include to(450px) {
                flex-direction: column; }
            display: flex;
            align-items: center;
            text-align: center;
            flex-direction: column;
            @include to(914px) {
                margin-bottom: 30px;
                &:last-child {
                    margin-bottom: 0; } }
            &__circle {
                border: 5px solid $palette-secondary-dark;
                border-radius: 100%;
                min-width: 170px;
                min-height: 170px;
                display: flex;
                align-items: center;
                justify-content: center;
                @include to(914px) {
                    min-width: 120px;
                    min-height: 120px; } }
            &__description {
                padding-top: 40px;
                @include to(914px) {
                    margin-left: 30px;
                    padding-top: 0;
                    text-align: left; }
                @include to(450px) {
                    text-align: center;
                    margin: auto;
                    margin-top: 10px; } } } } }

// Section consultation

.consultation {
    background: eurl('belaz_full.png') no-repeat bottom right 37px, #0d324d;
    padding: 100px 0;
    @include md(padding, 30px 0);
    .section-title {
        @include col();
        font-size: 27px;
        @include sm(font-size, 20px);
        width: 100%; }
    .call-us, .phone {
        @include col();
        width: 100%;
        text-align: center; }
    .call-us {
        font-size: 14px;
        margin-top: 60px; }
    .phone {
        margin-top: 16px;
        font-size: 30px;
        font-weight: 700;
        @include sm(font-size, 22px); }
    .form {
        display: flex;
        width: 100%;
        justify-content: center;
        align-items: center;
        margin-top: 60px;
        @include to(710px) {
            flex-direction: column; }
        .input-wrapper {
            margin-right: 30px;
            @include to(710px) {
                margin-right: 0;
                margin-bottom: 20px; } }
        .btn, .input {
            width: 370px;
            height: 70px; } } }

// Section Wolcomew to office

.welcome-to-office {
    color: $text-secondary;
    padding: 100px 0;
    @include md(padding, 30px 0);
    background: eurl('wide_belaz_second.png') no-repeat center center/cover;
    .section-title, .adress, .phone {
        width: 100%;
        text-align: center;
        @include col();
        @include size(12); }
    .adress {
        margin-top: 100px;
        @include md(margin-top, 30px); }
    .phone {
        margin-top: 34px;
        font-size: 24px;
        font-weight: 900; } }

// Section Map

.map {
    background-color: $palette-dark;
    iframe {
        border: 0;
        width: 100%;
        height: 375px; } }

// Footer

.footer {
    background-color: $palette-dark;
    @include md(padding, 30px 0);
    .row {
        height: 120px;
        align-items: center;
        @include md-block() {
            height: auto; } }
    .logo-wrapper {
        @include col();
        @include size(4);
        @include md-block() {
            @include size(12);
            display: flex;
            justify-content: center; } }
    .copy {
        @include col();
        @include size(4);
        @include size-md(12);
        text-align: center;
        @include md(margin-top, 10px); }
    .call-us {
        @include col();
        @include size(4);
        @include md-block() {
            @include size(12);
            text-align: center;
            margin-top: 10px; }
        text-align: right;
        .text {
            font-size: 13px; }
        .phone {
            font-size: 18px;
            font-weight: 700; } } }
